"use client";

import { useState } from "react";
import Image from "next/image";
import Link from "next/link";
import { 
  MapPin, MessageCircle, Info, Copy, Check, ShoppingBag, 
  X, Instagram, Search, Package, Phone 
} from "lucide-react";
import { Product, Store } from "@/types";

// Helper for TikTok Icon
const TiktokIcon = ({ size = 20 }: { size?: number }) => (
  <svg width={size} height={size} viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M9 12a4 4 0 1 0 4 4V4a5 5 0 0 0 5 5"/></svg>
);

interface StoreFrontProps {
  store: Store;
  products: any[];
  categories: { id: string; name: string }[];
}

type CartItem = { product: any; qty: number };

export default function StoreFront({ store, products, categories }: StoreFrontProps) {
  // State
  const [activeCategory, setActiveCategory] = useState("All");
  const [searchTerm, setSearchTerm] = useState("");
  const [isInfoModalOpen, setIsInfoModalOpen] = useState(false);
  const [copied, setCopied] = useState(false);
  
  // CART STATE
  const [cart, setCart] = useState<CartItem[]>([]);
  const [isCartOpen, setIsCartOpen] = useState(false);

  // --- ACTIONS ---
  const handleCopyLink = () => {
    navigator.clipboard.writeText(`${window.location.origin}/${store.slug}`);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  const addToCart = (product: any) => {
    setCart(prev => {
      const existing = prev.find(item => item.product.id === product.id);
      if (existing) {
        return prev.map(item => item.product.id === product.id ? { ...item, qty: item.qty + 1 } : item);
      }
      return [...prev, { product, qty: 1 }];
    });
  };

  const removeFromCart = (productId: string) => {
    setCart(prev => prev.filter(item => item.product.id !== productId));
  };

  // --- FILTERING ---
  const filteredProducts = products.filter(product => {
    const matchesSearch = product.name.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesCategory = activeCategory === "All" || product.categories?.name === activeCategory;
    return matchesSearch && matchesCategory;
  });

  // --- WHATSAPP LOGIC ---
  const cartTotal = cart.reduce((sum, item) => sum + (item.product.price * item.qty), 0);
  
  let cleanNumber = store.whatsapp_number.replace(/\D/g, '');
  if (cleanNumber.startsWith('0')) cleanNumber = '234' + cleanNumber.substring(1);

  const itemLines = cart.map(i => `- ${i.qty}x ${i.product.name} (₦${(i.product.price * i.qty).toLocaleString()})`).join('\n');
  const msg = `Hello ${store.name}, I want to place an order via StoreLink:\n\n${itemLines}\n\n*Total: ₦${cartTotal.toLocaleString()}*\n\nKindly provide me with your Account Details to complete payment and let me know the expected delivery day.`;
  const whatsappLink = `https://wa.me/${cleanNumber}?text=${encodeURIComponent(msg)}`;


  return (
    <div className="min-h-screen bg-gray-50 font-sans pb-20">
      
      {/* --- HEADER SECTION --- */}
      <div className="relative bg-white shadow-sm">
        {/* Cover Image */}
        <div className="h-40 md:h-56 bg-gray-200 relative overflow-hidden">
           {store.cover_image_url ? (
             <Image src={store.cover_image_url} alt="Cover" fill className="object-cover" />
           ) : (
             <div className="flex items-center justify-center h-full text-gray-400"><Image size={48}/></div>
           )}
        </div>
        
        <div className="max-w-6xl mx-auto px-4">
           <div className="flex flex-col md:flex-row items-center md:items-end -mt-12 md:-mt-16 mb-6 gap-6">
              {/* Logo */}
              <div className="relative w-24 h-24 md:w-32 md:h-32 rounded-full border-4 border-white shadow-md bg-white overflow-hidden shrink-0">
                 {store.logo_url ? (
                   <Image src={store.logo_url} alt="Logo" fill className="object-cover" />
                 ) : (
                   <div className="flex items-center justify-center h-full text-2xl font-bold bg-gray-100">{store.name.charAt(0)}</div>
                 )}
              </div>
              
              {/* Info */}
              <div className="flex-1 text-center md:text-left">
                 <h1 className="text-2xl md:text-3xl font-extrabold text-gray-900">{store.name}</h1>
                 <p className="text-gray-500 text-sm flex items-center justify-center md:justify-start gap-1 mt-1">
                   <MapPin size={14}/> {store.location}
                 </p>
              </div>

              {/* Actions */}
              <div className="flex gap-2">
                 <button onClick={() => setIsInfoModalOpen(true)} className="flex items-center gap-2 px-4 py-2 bg-gray-100 rounded-xl hover:bg-gray-200 text-gray-900 font-bold text-sm transition">
                    <Info size={18} /> Store Info
                 </button>
                 <button onClick={handleCopyLink} className="flex items-center gap-2 px-4 py-2 bg-gray-100 rounded-xl hover:bg-gray-200 text-gray-900 font-bold text-sm transition">
                    {copied ? <Check size={18} className="text-green-600"/> : <Copy size={18}/>}
                    {copied ? "Copied" : "Share"}
                 </button>
              </div>
           </div>
        </div>
      </div>

      {/* --- CATEGORY TABS & SEARCH --- */}
      <div className="sticky top-0 z-30 bg-white border-b border-gray-100 py-3 shadow-sm">
         <div className="max-w-6xl mx-auto px-4 flex gap-3 overflow-x-auto scrollbar-hide items-center">
            {/* Search Input */}
            <div className="relative min-w-[160px]">
               <Search className="absolute left-3 top-2.5 text-gray-400" size={16} />
               <input 
                 className="w-full pl-9 pr-4 py-2 bg-gray-100 rounded-full text-sm outline-none focus:ring-2 focus:ring-gray-900"
                 placeholder="Search..."
                 value={searchTerm}
                 onChange={e => setSearchTerm(e.target.value)}
               />
            </div>

            {/* Categories */}
            <button 
              onClick={() => setActiveCategory("All")}
              className={`px-4 py-2 rounded-full text-sm font-bold whitespace-nowrap transition flex-shrink-0 ${activeCategory === "All" ? "bg-gray-900 text-white" : "bg-gray-50 text-gray-600 hover:bg-gray-100"}`}
            >
              All Items
            </button>
            {categories.map(cat => (
              <button 
                key={cat.id}
                onClick={() => setActiveCategory(cat.name)}
                className={`px-4 py-2 rounded-full text-sm font-bold whitespace-nowrap transition flex-shrink-0 ${activeCategory === cat.name ? "bg-gray-900 text-white" : "bg-gray-50 text-gray-600 hover:bg-gray-100"}`}
              >
                {cat.name}
              </button>
            ))}
         </div>
      </div>

      {/* --- PRODUCT GRID --- */}
      <div className="max-w-6xl mx-auto px-4 py-8">
         {filteredProducts.length === 0 ? (
            <div className="text-center py-20">
               <Package className="w-16 h-16 text-gray-200 mx-auto mb-4"/>
               <p className="text-gray-500">No products found.</p>
            </div>
         ) : (
            <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4 md:gap-6">
               {filteredProducts.map(product => (
                 <div key={product.id} className="bg-white rounded-2xl overflow-hidden border border-gray-100 shadow-sm hover:shadow-md transition group flex flex-col">
                    <Link href={`/product/${product.id}`} className="block relative aspect-square bg-gray-100">
                       {product.image_urls?.[0] ? (
                         <Image src={product.image_urls[0]} alt={product.name} fill className="object-cover group-hover:scale-105 transition duration-500" />
                       ) : (
                         <div className="flex items-center justify-center h-full text-gray-300"><Image size={32}/></div>
                       )}
                    </Link>
                    <div className="p-4 flex flex-col flex-1">
                       <h3 className="font-bold text-gray-900 truncate text-sm md:text-base">{product.name}</h3>
                       <p className="text-xs text-gray-500 mb-2">{product.categories?.name}</p>
                       <div className="mt-auto flex items-center justify-between">
                          <p className="text-emerald-600 font-extrabold">₦{product.price.toLocaleString()}</p>
                          <button 
                            onClick={() => addToCart(product)}
                            className="p-2 bg-gray-900 text-white rounded-full hover:bg-gray-700 transition shadow-lg active:scale-90"
                          >
                            <ShoppingBag size={16} />
                          </button>
                       </div>
                    </div>
                 </div>
               ))}
            </div>
         )}
      </div>

      {/* --- FLOATING CART BUTTON --- */}
      {cart.length > 0 && !isCartOpen && (
        <button 
          onClick={() => setIsCartOpen(true)} 
          className="fixed bottom-6 right-6 bg-gray-900 text-white p-4 rounded-full shadow-2xl z-40 hover:scale-105 transition animate-in zoom-in"
        >
           <ShoppingBag size={24} />
           <span className="absolute -top-1 -right-1 bg-red-500 w-6 h-6 rounded-full flex items-center justify-center text-xs font-bold border-2 border-white">{cart.length}</span>
        </button>
      )}

      {/* --- CART SIDEBAR --- */}
      {isCartOpen && (
        <div className="fixed inset-0 z-[60] bg-black/60 backdrop-blur-sm flex justify-end animate-in fade-in">
           <div className="absolute inset-0" onClick={() => setIsCartOpen(false)}></div>
           <div className="relative w-[85vw] md:w-full md:max-w-md bg-white h-full shadow-2xl p-6 overflow-y-auto animate-in slide-in-from-right duration-300">
             <div className="flex justify-between items-center mb-6">
                <h2 className="text-xl font-bold flex items-center gap-2"><ShoppingBag /> Your Bag</h2>
                <button onClick={() => setIsCartOpen(false)} className="p-2 bg-gray-100 rounded-full hover:bg-gray-200"><X size={20}/></button>
             </div>

             {cart.length === 0 ? (
                <p className="text-gray-500 text-center py-10">Bag is empty.</p>
             ) : (
                <>
                  <div className="space-y-4 mb-6">
                    {cart.map(item => (
                      <div key={item.product.id} className="flex justify-between text-sm border-b border-gray-50 pb-2">
                         <div>
                            <p className="font-bold text-gray-900">{item.product.name}</p>
                            <p className="text-gray-500">{item.qty} x ₦{item.product.price.toLocaleString()}</p>
                         </div>
                         <button onClick={() => removeFromCart(item.product.id)} className="text-red-400 hover:text-red-600"><X size={18}/></button>
                      </div>
                    ))}
                  </div>
                  
                  <div className="flex justify-between items-center text-lg font-bold border-t border-gray-100 pt-4 mb-6">
                     <span>Total</span>
                     <span className="text-emerald-600">₦{cartTotal.toLocaleString()}</span>
                  </div>

                  <a href={whatsappLink} target="_blank" className="flex items-center justify-center gap-2 w-full bg-green-600 text-white py-4 rounded-xl font-bold hover:bg-green-700 transition shadow-lg">
                     <MessageCircle size={20} /> Checkout on WhatsApp
                  </a>
                </>
             )}
           </div>
        </div>
      )}

      {/* --- INFO MODAL (Redesigned) --- */}
      {isInfoModalOpen && (
         <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/60 backdrop-blur-sm p-4 animate-in fade-in">
            <div className="bg-white w-full max-w-sm rounded-3xl p-6 shadow-2xl relative">
               <div className="flex justify-between items-center mb-6">
                  <h2 className="font-bold text-xl text-gray-900">About Store</h2>
                  <button onClick={() => setIsInfoModalOpen(false)} className="p-2 bg-gray-100 rounded-full hover:bg-gray-200"><X size={20}/></button>
               </div>
               
               <div className="text-center mb-6">
                  <div className="relative w-20 h-20 bg-gray-100 rounded-full mx-auto mb-3 overflow-hidden border-2 border-gray-100">
                      {store.logo_url ? (
                          <Image src={store.logo_url} alt="Logo" fill className="object-cover" />
                      ) : (
                          <div className="flex items-center justify-center h-full font-bold">{store.name.charAt(0)}</div>
                      )}
                  </div>
                  <h3 className="font-bold text-lg">{store.name}</h3>
                  <p className="text-gray-500 text-sm flex items-center justify-center gap-1 mt-1">
                     <MapPin size={14}/> {store.location}
                  </p>
               </div>

               <p className="text-gray-700 text-sm text-center mb-6 italic bg-gray-50 p-3 rounded-xl border border-gray-100">
                  {store.description || "Welcome to our store! We sell amazing products."}
               </p>
               
               <div className="space-y-3">
        
                   {store.tiktok_url && (
                       <a href={store.tiktok_url} target="_blank" className="flex items-center justify-center gap-3 w-full py-3 bg-gray-100 text-black rounded-xl font-bold hover:bg-gray-200 transition">
                           <TiktokIcon size={20} /> TikTok
                       </a>
                   )}
                   
                   {store.instagram_handle && (
                       <a href={`https://instagram.com/${store.instagram_handle}`} target="_blank" className="flex items-center justify-center gap-3 w-full py-3 bg-pink-50 text-pink-700 rounded-xl font-bold hover:bg-pink-100 transition">
                           <Instagram size={20} /> Instagram
                       </a>
                   )}
               </div>
            </div>
         </div>
      )}

    </div>
  );
}